
.markdown-container {
  max-width: 980px; margin: auto;
  color: #24292e;
}
.markdown-container p {
  box-sizing: border-box;
  display: block;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
  margin-top: 0;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  word-wrap: break-word;
}
.markdown-container h1 {
  border-bottom: 1px solid #eaecef;
  box-sizing: border-box;
  display: block;
  font-size: 32px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 16px;
  padding-bottom: 0.3em;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  word-wrap: break-word;
}
.markdown-container h2 {
  border-bottom: 1px solid #eaecef;
  box-sizing: border-box;
  display: block;
  font-size: 24px;
  font-weight: 600;
  line-height: 16px;
  margin-bottom: 16px;
  margin-top: 24px;
  padding-bottom: 0.3em;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  word-wrap: break-word;
}
.markdown-container table {
  color: #24292e;
  border-collapse: collapse;
  border-bottom-color: grey;
  border-left-color: grey;
  border-top-color: grey;
  border-right-color: grey;
  font-size: 16px;
  line-height: 24px;
  overflow-x: auto;
  overflow-y: auto;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  word-wrap: break-word;
  display: block;
  width: 100%;
  overflow: auto;
}
.markdown-container table th {
    font-weight: 600;
}
.markdown-container table th,.markdown-container table td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
}
.markdown-container table tr {
    background-color: #fcf8ff;
    border-top: 1px solid #c6cbd1;
}
.markdown-container table tr:nth-child(2n) {
    background-color: #faf3ff;
}
.markdown-container table img {
    background-color: transparent;
}
.markdown-container img {
    max-width: 100%;
    box-sizing: content-box;
    background-color: #fff;
}
.markdown-container img[align=right] {
    padding-left: 20px;
}
.markdown-container img[align=left] {
    padding-right: 20px;
}



